@import '~antd/dist/antd.compact.css';

.logo {
  padding-right: 32px;
}

.topContentHome {
  padding: 24px 10px;
  text-align: center;
  min-height: 300px;
  background-color: #F3C623;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.topContentHomeAction {
  margin-top: 20px;
}

.container {
  margin: 0px 6em;
}

.btnPrimary {
  padding: 10px;
  background-color: #001529;
  color: #ffffff;
  font-size: 1.2em;
  border-radius: 5px;
  margin: 10px;
}

.btnLink {
  padding: 10px;
}

.jobItem {
  background-color: #001529;
  color: #ffffff;
  padding: 10px 20px;
  border-radius: 5px;
  width: 100%;
}

.container-fluid {
  padding: 20px 100px;
}

.container-loading {
  display: flex;
  justify-content: center;
  margin: 50px 0;
}

.about-section {
  background-color: #F3C623;
  padding: 50px 100px;
}

@media (max-width: 767px) {
  .container {
    margin: 0 20px;
  }

  .jobItem {
    margin: 0;
  }
  .container-fluid {
    padding: 20px 20px;
  }
  .about-section {
    padding: 20px 20px;
  }

  .leftMenu {
    display: flex;
    justify-content: space-between;
  }
}